h1, h2, h3, h4 {
    margin: 0;
}
.text {
    all: unset;
    display: flex;
    align-items: center;
    font-size: calc(5px + 1vw);
}
.pageSetup{
    position: relative;
    margin: auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
.header-bg {
    background-color: #1b2d3c;
}
.nav-bg {
    background-color: #12171b;
}
.home-bg {
    background-color: #1b2d3c;
}
.detailPage-bg{
    background-color: #1b2d3c;

}
.container {
    display: flex;
    position: relative;
    width: 90%;
    max-width: 1500px;
    gap: 5%;
    padding: 1rem;
}
.main-container {
    display: flex;
    position: inherit;
    border-radius: 3px;
    width: 69%;
    padding: 1rem;
    text-align: center;
}
.aside-container {
    display: flex;
    position: inherit;
    border-radius: 3px;
    width: 26%;
    padding: 1rem;
    text-align: center;
}
.header {
    display: flex;
    flex-direction: column;
    height: 120px;
    width: 100%;
    border-bottom: 1px solid gray;
    box-shadow: 0px 0px 1px 0px gray;
    /*opacity: 0;*/
}
.header-contain {
    display: flex;
    height: 120px;
    justify-content: center;
    align-items: center;
    color:antiquewhite
}
.nav-contain {
    display: flex;
    color:antiquewhite;
    flex-wrap: wrap;
}
.home-contain {
    display: flex;
    color:antiquewhite;
    flex-direction: column;
    background-color: #12171b30;
    margin: 2rem 0;
    box-shadow: 20px 20px 5px 5px rgba(0, 0, 0, 0.2);
}
.detail-contain{
    display: flex;
    color:antiquewhite;
    background-color: #12171b30;
    margin: 2rem 0;
    box-shadow: 20px 20px 5px 5px rgba(0, 0, 0, 0.2);
    flex-wrap: wrap;
}

.detail-1 {
    position: relative;
    width: 30%;
    height: 20rem;
    border-radius: 5px;
}
.detail-3 {
    border-top: 2px gray solid;
    width: 100%;
    padding-top: 2rem;
}
.detail-2 {
    width: 68%;
    text-align: left;
    margin-left: 2%;
    height: 25rem;
    border: 1px rgb(176, 141, 141) solid;
    border-radius: 5px;
    padding: 1rem;
    display: flex;
    flex-direction: column;
}
.info-text {
    line-height: 1;
    margin: 0;
    padding: 0;
    border-bottom: #3f474e 1px solid;
    height: 3rem;
    display: flex;
    align-items: center;
}
.nav-list-contain {
    margin: 0;
    padding: 0 0rem 0 0rem;
    display: flex;
    align-items: left;
}
.nav-list {
    display: flex;
}
.genresList, .navItem, .home {
    cursor: pointer;
    margin: 1rem 1rem 1rem 0rem;
    font-size: calc(5px + 1vw);
}
.fa-house {
    font-size: calc(5px + 1vw);
    padding-right: 0.5rem;
}
.home {
    margin-left: 1rem;
    display: flex;
    align-items: center;
}

.logo {
    flex: 1 0 2rem;
    display: flex;
    min-width: 2rem;
    align-items: center;
    justify-content: center;
}
.logo img {
    cursor: pointer;
    width: auto;
    height: 50px;
}
.search-contain {
    position: relative;
    flex: 2 0 2rem;
}
.searchBar{
    position: relative;
    margin: 1rem 0.1rem;
    width: 100%;
    height: 2rem;
    cursor: text;
    text-align: center;
    border-radius: 50px;
    box-shadow: inset 2px 2px 5px #727272, inset -2px -2px 5px #ccc;
    padding: 10px;
    border: none;
    background-color: #cecece;
    outline: none;
}
.searchBar:focus {
    outline: none;
    background-color: #e0e0e0;
}
.search-icon {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    color: #12171b;
}
.user-icon {
    flex: 1 0 2rem;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    gap: 1rem;
}

.footer {
    display: flex;
    height: 70px;
    border-top: 1px solid gray;
    box-shadow: 1px 0px 0px 0px gray;
    justify-content: center;
    background-color: #12171b;
}
.footerText{
    display: flex;
    align-items: center;
    color: wheat;
}

.buttonContain{
    position: relative;
    display: flex;
    justify-content: center;
    width: 100%;
    max-width: 1300px;
    padding: 0 0.2rem;
    bottom: 0;
    opacity: 1;
    z-index: 10;
    background-color: #1b2d3c;
    gap: 20%
}
.buttonPages {
    position: relative;
    display: flex;
    justify-content: center;
    opacity: 1;
    z-index: 1;
}
.button-special{
    width: 30% !important;
}
.button{
    position: relative;
    margin: 0.5rem 0.1rem;
    width: 3rem;
    height: 3rem;
    background-color: #12171b ;
    cursor: pointer;
    border: #20272ba4 1px solid;
   color: antiquewhite;
   border-radius: 5px;
   box-shadow: 1px 1px 1px 1px #253036a4 inset;
}
.pageInput{
    position: relative;
    margin: 0.5rem 0.5rem;
    width: 3rem;
    height: 3rem;
    cursor: text;
    text-align: center;
    border-radius: 5px;
    box-shadow: inset 2px 2px 5px #727272, inset -2px -2px 5px #ccc;
    padding: 10px;
    border: none;
    background-color: #cecece;
}
.pageInput:focus{
    box-shadow: inset 2px 2px 5px #727272, inset -2px -2px 5px #ccc;
    padding: 10px;
    border: none;
    background-color: #e0e0e0;
    outline: none;
}
.active {
    background-color: orange;
}
.hidden {
    display: none;
}
.group {
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.genresList, .genresList:active, .genresList:hover, .genresList:focus {
    display: flex;
    flex-wrap: wrap;
    border: solid 1px gray;
    border-radius: 5px;
    color: antiquewhite;
    background-color: #12171b;
    outline: none;
    line-height: 100%;
}
.homeButton {
    all:unset;
    display: flex;
    align-items: center;
}
.main-title {
    display: flex;
    position: relative;
}
.line {
    position: relative;
    width: 100%;
    height: 1px;
    margin: 0.2rem 0 1rem 0;
    background-color: aquamarine;
    z-index: 2;
}
.black-line {
    width: 100%;
    height: 1px;
    margin: 0.2rem 0 1rem 0;
    background-color: rgba(255, 255, 255, 0.292);
    position: absolute;
    bottom: 0;
}
.genresItem {
    cursor: pointer;
}
.movie-grid {
    display: grid;
    grid-template: auto/ repeat(4, 1fr);
    grid-gap: 1rem;
    padding: 0;
    margin-bottom: 1rem;
}
.movieItem {
    position: relative;
    border-radius: 5px;
    cursor: pointer;
    aspect-ratio: 1 / 1;
}
.movie-title {
    border-radius: inherit;
    position: absolute;
    font-size: calc(5px + 0.6vw);
    min-height: 3rem;
    padding: 0.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    z-index: 1;
    background-color: hsla(207, 38%, 17%, 0.4);
    bottom: 0rem;
    color: rgba(240, 248, 255, 0.785);
}
.movie-bg {
    border-radius: inherit;
    position: relative;
    height: 100%;
}
.movie-img {
    border-radius: inherit;
    position: relative;
    bottom: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.aside-movie-grid {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    background-color: #12171b;

}
.aside-movieItem {
    border-radius: inherit;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border-bottom: 1px solid gray;
}
.aside-movie-title {
    border-radius: inherit;
    width: 50%;
    padding: 1rem;
}
.movie-title-detail {
    min-height: 5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
.aside-movie-bg{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: inherit;
    width: 50%;
}
.focus {
    opacity: 1;
    color: green;
    background-color:aliceblue;
    transition: all 0.5s linear 0.2s;
}
.visible {
    opacity: 1;
    transition: all 0.5s linear 0.2s;
}
.invisible {
    opacity: 0;
    transition: all 0.5s linear 0.2s;
}
.main {
    min-height: 80svh;
}
@media screen and (max-width: 500px) {
    .pageSetup{
        max-width: 500px;
        margin: auto;
    }
    h1 {
        font-size: 1.5rem;
    }
    .container {
        padding: 0.5rem;
        gap: 5px;
    }
    .movie-grid {
        grid-gap: 0.3rem;
        grid-template: auto/ repeat(3, 1fr);
    }
    .main-container{
        width: 100%;
        padding: 0.5rem;
    }
    .aside-container {
        display: none;
    }
    .genresList, .navItem, .home {
        cursor: pointer;
        margin: 0.5rem 0.5rem 0.5rem 0rem;
    }
    .movie-img{
        height: 15rem;
        border-radius: 5px;
    }
    .detail-1 {
        height: 20rem;
        width: 100%;
        margin-bottom: 1rem;
    }
    .detail-2 {
        height: 20rem;
        margin-bottom: 1rem;
        width: 100%;
        margin-left: 0;
    }
    .detail-2 {

    }
}